import React from "react";
import classNames from "classnames";
import {Avatar, Link, Tooltip, withStyles} from "@material-ui/core";
import Header from "../../components/Header/Header";
import HeaderLinks from "../../components/Header/HeaderLinks";
import Footer from "../../components/Footer/Footer";
import GridContainer from "../../components/Grid/GridContainer";
import GridItem from "../../components/Grid/GridItem";

import styles from "./styles";
import Card from "../../components/Card/Card";
import vidaProject from "../../assets/img/projects/vida.png";
import relmeProject from "../../assets/img/projects/relme.png";
import uciCiencia from "../../assets/img/projects/uciciencia.png";
import fici from "../../assets/img/projects/fici.png";
import defaultPng from "../../assets/img/projects/default.png";
import CardBody from "../../components/Card/CardBody";
import CardFooter from "../../components/Card/CardFooter";

class Projects extends React.Component {
    render() {
        const {classes, theme, ...rest } = this.props;

        return (
            <div>
                <Header
                    brand="Fbrica Introductoria de Aplicaciones Informticas"
                    rightLinks={<HeaderLinks />}
                    fixed
                    color="white" />

                <Card className={classes.container}>
                    <div style={{marginLeft: "auto", marginRight: "auto"}}>
                        <h2 className={classes.title}>Nuestros proyectos</h2>
                    </div>
                    <GridContainer>
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <Avatar src={vidaProject} alt="..." className={classNames(classes.img, classes.centeredImg)} />
                                <h4 className={classes.cardTitle}>
                                    Regalando vidas
                                    <br />
                                    {/*<small className={classes.smallTitle}>Model</small>*/}
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        Aplicacin para sistemas Android para homenajear la
                                        labor de los donantes de Sangre de la Universidad de
                                        las Ciencias Informticas.<br/><br/>
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Tooltip title="Ver cdigo fuente en Github">
                                        <Link
                                            href="https://github.com/fiai-team/regalando_vidas"

                                            className={classes.margin5}>
                                            <i className={classes.socials + " fab fa-github"} />
                                        </Link>
                                    </Tooltip>
                                </CardFooter>
                            </Card>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <Avatar src={relmeProject} alt="..." className={classNames(classes.img, classes.centeredImg)} />
                                <h4 className={classes.cardTitle}>
                                    Relme 33
                                    <br />
                                    {/*<small className={classes.smallTitle}>Model</small>*/}
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        Aplicacin informativa para sistemas Android acerca de
                                        La Reunin Latinoamericana de Matemtica Educativa (RELME), que se desarroll
                                        en La Habana, Cuba.
                                        <br/><br/>
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Tooltip title="Ver cdigo fuente en Github">
                                        <Link
                                            href="https://github.com/fiai-team/Relme33"

                                            className={classes.margin5}>
                                            <i className={classes.socials + " fab fa-github"} />
                                        </Link>
                                    </Tooltip>
                                </CardFooter>
                            </Card>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <Avatar src={uciCiencia} alt="..." className={classNames(classes.img, classes.centeredImg)} />
                                <h4 className={classes.cardTitle}>
                                    UCICiencia
                                    <br />
                                    {/*<small className={classes.smallTitle}>Model</small>*/}
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        Aplicacin informativa para sistemas Android acerca de
                                        III Conferencia Cientfica Internacional (2018), que se desarroll
                                        en La Universidad de las Ciencias Informticas (UCI).
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Tooltip title="Ver cdigo fuente en Github">
                                        <Link
                                            href="https://github.com/fiai-team/UCI_Ciencia"
                                            className={classes.margin5}>
                                            <i className={classes.socials + " fab fa-github"} />
                                        </Link>
                                    </Tooltip>
                                </CardFooter>
                            </Card>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <Avatar src={fici} alt="..." className={classNames(classes.img, classes.centeredImg)} />
                                <h4 className={classes.cardTitle}>
                                    FICI Primeros das
                                    <br />
                                    {/*<small className={classes.smallTitle}>Model</small>*/}
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        Aplicacin informativa para sistemas Android para
                                        ayudar a los estudiantes de nuevo ingreso durante sus primeros
                                        das en La Universidad de las Ciencias Informticas (UCI).
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Tooltip title="Ver cdigo fuente en Github">
                                        <Link
                                            href="https://github.com/fiai-team/FICI_Primeros_Dias"
                                            className={classes.margin5}>
                                            <i className={classes.socials + " fab fa-github"} />
                                        </Link>
                                    </Tooltip>
                                </CardFooter>
                            </Card>
                        </GridItem>
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <Avatar src={defaultPng} alt="..." className={classNames(classes.img, classes.centeredImg)} />
                                <h4 className={classes.cardTitle}>
                                    Evaluacin Institucional
                                    <br />
                                    {/*<small className={classes.smallTitle}>Model</small>*/}
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        Aplicacin para sistemas Android para apoyar para el Proceso de Acreditacin de la
                                        Universidad de las Ciencias Informticas (UCI).
                                        <br/>
                                        <br/>
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Tooltip title="Ver cdigo fuente en Github">
                                        <Link
                                            href="https://github.com/fiai-team/Evaluacion-institucional"
                                            className={classes.margin5}>
                                            <i className={classes.socials + " fab fa-github"} />
                                        </Link>
                                    </Tooltip>
                                </CardFooter>
                            </Card>
                        </GridItem>
                        {/*
                        <GridItem xs={12} sm={12} md={4}>
                            <Card plain>
                                <GridItem xs={12} sm={12} md={6} className={classes.itemGrid}>
                                    <Avatar src={facultyPortalProject} alt="..." className={imageClasses} />
                                </GridItem>
                                <h4 className={classes.cardTitle}>
                                    Christian Louboutin
                                    <br />
                                    <small className={classes.smallTitle}>Designer</small>
                                </h4>
                                <CardBody>
                                    <p className={classes.description}>
                                        You can write here details about one of your team members. You
                                        can give more details about what they do. Feel free to add
                                        some <a href="#pablo">links</a> for people to be able to
                                        follow them outside the site.
                                    </p>
                                </CardBody>
                                <CardFooter className={classes.justifyCenter}>
                                    <Button
                                        justIcon
                                        color="transparent"
                                        className={classes.margin5}
                                    >
                                        <i className={classes.socials + " fab fa-twitter"} />
                                    </Button>
                                    <Button
                                        justIcon
                                        color="transparent"
                                        className={classes.margin5}
                                    >
                                        <i className={classes.socials + " fab fa-linkedin"} />
                                    </Button>
                                </CardFooter>
                            </Card>
                        </GridItem>*/}
                    </GridContainer>


                </Card>

                <Footer />

            </div>
        );
    }
}

export default  withStyles(styles, {withTheme: true})(Projects);
